// Importa estilos globais e bibliotecas necessrias
import { Link } from 'react-router-dom';
import './erro.scss';
import '../../utils/cssConf.scss';

// Define o componente funcional da pgina Erro
function Erro() { 
    return (
        <div className='not-found'>
            <h1>404</h1>
            <h2>Pgina no encontrada!</h2>
            <Link className='buttom-link mt-14 p-10' to="/">Voltar para a tela principal</Link> {/* Link para retornar  pgina principal */}
        </div>
    );
}

export default Erro;