// Importa o estilo globail e imagens necessrias
import './cardFunc.css';
import link from '../../img/link.png';
import qrcode from '../../img/qrcode.png';
import balance from '../../img/balance.png';
import time from '../../img/time.png';

function CardFunc() {
  return (
    <div className='funcLink-container'>
      <h1 className='mb-20 primary-text '>Funcionalidades</h1> {/* Ttulo do componente */}
      <div className='container-card'>
        {/* Primeiro conjunto de cartes */}
        <div className='card-base'>
          <img className='img-res' src={link} alt="cone de link" /> {/* cone de link */}
          <div className='area-text mbl-10 font-bold'>
            <p className='primary-text mb-10 fs-16'>Encurtar links</p> {/* Ttulo do carto */}
            <p className='fs-10 secundary-text'>Deixando seu link menor, facilita o compartilhamento</p> {/* Descrio do carto */}
          </div>
        </div>
        <div className='card-base'>
          <img className='img-res' src={qrcode} alt="cone de QR code" /> {/* cone de QR code */}
          <div className='area-text mbl-10 font-bold'>
            <p className='primary-text mb-10 fs-16'>Qr code</p> {/* Ttulo do carto */}
            <p className='fs-10 secundary-text'>Facilita o compartilhamento de links gerados!</p> {/* Descrio do carto */}
          </div>
        </div>
      </div>
      <div className='container-card'>
        {/* Segundo conjunto de cartes */}
        <div className='card-base'>
          <img className='img-res' src={balance} alt="cone de anlise" /> {/* cone de anlise */}
          <div className='area-text mbl-10 font-bold'>
            <p className='primary-text mb-10 fs-16'>Anlise de dados</p> {/* Ttulo do carto */}
            <p className='fs-10 secundary-text'>Melhor controle/gerenciamento dos links da sua empresa.</p> {/* Descrio do carto */}
          </div>
        </div>
        <div className='card-base'>
          <img className='img-res' src={time} alt="cone de tempo" /> {/* cone de tempo */}
          <div className='area-text mbl-10 font-bold'>
            <p className='primary-text mb-10 fs-16'>Tempo de validade</p> {/* Ttulo do carto */}
            <p className='fs-10 secundary-text'>Nosso servio mantm ativos os links por 1 ano!</p> {/* Descrio do carto */}
          </div>
        </div>
      </div>
    </div>
  );
};

export default CardFunc;