// Importa estilos globais e bibliotecas necessrias
import './cover.scss';
import '../../utils/cssConf.scss'
import { useNavigate } from 'react-router-dom';

function Cover() {
    const navigate = useNavigate(); // Hook para navegao

    // Manipula o clique no ttulo para navegar para a pgina inicial
    const handleClick = () => {
        navigate('/');
    };

    return (
        <div className="background-cover">
            <div className="cover-info"> {/* Informaes do cover */}
                <div>
                    <h1 className='fs-40 titulo-click' onClick={handleClick}>Briefly</h1> {/* Ttulo com clique para navegar */}
                    <p className='fs-20'>Encurtando links</p> {/* Descrio */}
                </div>
                <p className='fs-12'>Soluo rpida e prtica para encurtamento de links.</p> {/* Subdescrio */}
            </div>
        </div>
    )
}

export default Cover;